import axios from 'axios'

axios.defaults.withCredentials = true;

import {
    CONFIG_HEADER,
    CONFIG_HEADER_FILE
} from '../admin/urls'

export default {
    DEBGU: true,
    accessToken: sessionStorage.getItem('accessToken'),
    version:'1.0.0',
    imgHost:'/',
    post(url, frm, afterFunc, errorFunc) {
        this.checkDebug();
        if (this.accessToken) {
            CONFIG_HEADER.Authorization = 'Bearer '+this.accessToken
        }
        axios.post(url, frm, {headers: CONFIG_HEADER}).then(res => {

            if (this.DEBGU) {
                console.log(res)
            }
            switch (res.data.status) {
                case 'success':
                case 'message':
                    afterFunc(res.data);
                    break;
                case 'error':
                    alert('결과값이 없습니다');
                    break;
                case 'fail':
                    alert('잘못된 접근입니다');
                    break;
            }

        }).catch((error) => {
            if(errorFunc) {
                errorFunc(error)
            } else {
                this.setError(error);
            }


        });
    },
    postFile(url, frm, afterFunc) {
        this.checkDebug();
        if (this.accessToken) {
            CONFIG_HEADER_FILE.Authorization = 'Bearer '+this.accessToken
        }
        axios.post(url, frm, {headers: CONFIG_HEADER_FILE}).then(res => {
            if (this.DEBGU) {
                console.log(res.data)
            }
            switch (res.data.status) {
                case 'success':
                case 'message':
                    afterFunc(res.data);
                    break;
                case 'error':
                    alert('결과값이 없습니다');
                    break;
                case 'fail':
                    alert('잘못된 접근입니다');
                    break;
            }
        }).catch((error) => {
            this.setError(error);

        });
    },
    get(url, frm, afterFunc, errorFunc) {
        this.checkDebug();

        axios.get(url, {params: frm, headers: CONFIG_HEADER}).then(res => {
            if (this.DEBGU) {
                console.log(res.data)
            }
            if (res.data.status == 'notLogin') {
                location.href = '/';
                return;
            }

            switch (res.data.status) {
                case 'success':
                case 'message':
                    afterFunc(res.data);
                    break;
                case 'error':
                    alert('결과값이 없습니다');
                    location.href = '/';
                    break;
                case 'fail':
                    alert('잘못된 접근입니다');
                    location.href = '/';
                    break;
            }


        }).catch((error) => {
            if (errorFunc) errorFunc(error.response.data)
            else this.setError(error);


        });
    },
    checkDebug() {
        if (location.hostname == 'localhost') {
            this.DEBGU = true;
            this.imgHost = 'http://localhost/';
        } else this.DEBGU = false;
    },
    setError(error) {

        switch (error.response.status) {
            case 500:
                this.accessToken = '';
                CONFIG_HEADER_FILE.Authorization = ''
                location.href = '/';
                break;
            default:  // 이미지 업로드 에러
                alert('잘못된 접근입니다');
                this.accessToken = '';
                CONFIG_HEADER_FILE.Authorization = ''
                location.href = '/';
                break;
        }

        if (!this.DEBGU) return;
        if (error.response) {
            // 요청이 이루어졌으며 서버가 2xx의 범위를 벗어나는 상태 코드로 응답했습니다.
            console.log(error.response.data);
            console.log(error.response.status);
            console.log(error.response.headers);
        } else if (error.request) {
            // 요청이 이루어 졌으나 응답을 받지 못했습니다.
            // `error.request`는 브라우저의 XMLHttpRequest 인스턴스 또는
            // Node.js의 http.ClientRequest 인스턴스입니다.
            console.log(error.request);
        } else {
            // 오류를 발생시킨 요청을 설정하는 중에 문제가 발생했습니다.
            console.log('Error', error.message);
        }

    }


}
