<template>
    <div class="page-container">
        <div class="product-container">
            <form novalidate @submit.prevent="validate">
                <input type="file" style="display: none;" ref="img" accept="image/*" @change="imgUpload"/>
                <input type="file" style="display: none;" ref="downFile" @change="dfileUpload"/>

                <div class="product-regist">
                    <div class="product-title">기본정보</div>
                    <div class="product-form">
                        <table class="table-regist" cellspacing="0" cellpadding="0" border="0">
                            <tbody>
                            <tr>
                                <th><span class="required-icon">*</span>게시판 선택</th>
                                <td colspan="3">
                                    <div class="select-box" id="bid-required">
                                        <v-select ref="bid" v-model="bid"
                                                  dense
                                                  :rules="[() => !!bid || '게시판을 선택하세요']"
                                                  outlined
                                                  required
                                                  :disabled="bidDisabled"
                                                  label="게시판 선택"
                                                  style="max-width:330px;"
                                                  :items="bidList">

                                        </v-select>
                                    </div>
                                </td>
                            </tr>
                            <tr v-if="userInfoShow">
                                <th><span class="required-icon">*</span>작성자</th>
                                <td id="userinfo-required">
                                    <div class="input-box">
                                        <v-text-field v-model="userName"
                                                      :rules="[() => !!userName || '작성자명을 입력하세요']"
                                                      required
                                                      dense
                                                      ref="userName"
                                                      maxLength="20">

                                        </v-text-field>
                                    </div>
                                </td>
                                <th style="border-left:none;"><span class="required-icon">*</span>비밀번호</th>
                                <td>
                                    <div class="input-box">
                                        <v-text-field v-model="userPass"
                                                      :rules="[() => !!userPass || '비밀번호를 입력하세요']"
                                                      required
                                                      ref="userPass"
                                                      type="password"
                                                      maxLength="10"
                                                      dense>

                                        </v-text-field>
                                    </div>
                                </td>
                            </tr>
                            <tr v-if="categoryUse=='yes'">
                                <th><span class="required-icon">*</span>카테고리</th>
                                <td id="category-required" colspan="3">
                                    <div class="select-box">
                                        <v-select v-model="category"
                                                  dense
                                                  outlined
                                                  :rules="[() => !!category || '카테고리를 선택하세요']"
                                                  required
                                                  ref="category"
                                                  label="카테고리를 선택하세요"
                                                  style="max-width:330px;"
                                                  :items="categoryList">

                                        </v-select>
                                    </div>
                                </td>
                            </tr>
                            <!--tr v-if="secretUse=='yes'">
                                <th>비밀글 여부</th>
                                <td>
                                    <div class="radio-box" style="max-height:40px;">
                                        <v-checkbox
                                                v-model="secret"
                                                label="비밀글"
                                                value="yes"
                                        ></v-checkbox>
                                    </div>
                                </td>
                            </tr-->
                            <tr>
                                <th><span class="required-icon">*</span>제목</th>
                                <td id="subject-required" colspan="3">
                                    <div class="input-box" style="width:830px;">
                                        <v-text-field v-model="subject"
                                                      :rules="[() => !!subject || '제목을 입력하세요']"
                                                      required
                                                      ref="subject"
                                                      maxLength="45"
                                                      dense>

                                        </v-text-field>

                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <th><span class="required-icon">*</span>내용</th>
                                <td colspan="3">
                                    <div class="quill-content-box" style="width:830px;padding-bottom:12px;">
                                        <quill-editor ref="myQuillEditor" :content="content"
                                                      :options="editorOption"
                                                      @change="onEditorChange($event)">
                                        </quill-editor>

                                    </div>
                                </td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="product-title" style="padding-top:30px;">추가정보</div>
                    <div class="product-form">
                        <table class="table-regist" cellspacing="0" cellpadding="0" border="0">
                            <tbody>
                            <tr>
                                <th>태그</th>
                                <td>
                                    <div class="input-box" style="width:830px;">
                                        <v-text-field v-model="keyword"
                                                      placeholder="쉼표(,)를 이용하여 복수등록 가능"
                                                      dense>
                                        </v-text-field>
                                    </div>
                                </td>
                            </tr>

                            <tr>
                                <th>파일첨부</th>
                                <td>
                                    <v-btn class="white-button" depressed type="button" @click="addDfile()">파일첨부</v-btn>
                                    <div class="detail-file-box" style="padding-top:5px;">
                                        <v-chip
                                                v-for="(dfile,index) in dFileList"
                                                class="ma-2"
                                                close
                                                color="green"
                                                outlined
                                                @click:close="removeDfile(index)"
                                        >
                                            {{dfile.orgFileName}}
                                        </v-chip>
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <th>추가설정</th>
                                <td style="padding-bottom:20px;">
                                    <div class="input-box" style="display: flex;margin-top:-10px;">
                                        <v-checkbox color="#000" v-model="replyUse"
                                                    value="yes" style="margin-right:20px" label="댓글 허용"></v-checkbox>
                                        <v-checkbox v-model="userNotice"
                                                    value="yes" color="#000" style="margin-right:20px"
                                                    label="알림"></v-checkbox>
                                        <v-checkbox v-model="open"
                                                    value="yes" color="#000" style="margin-right:20px"
                                                    label="공개"></v-checkbox>
                                        <v-checkbox v-model="notice" color="#000"
                                                    value="yes" label="공지"></v-checkbox>
                                    </div>
                                </td>
                            </tr>
                            </tbody>
                        </table>
                    </div>


                </div>
                <div class="button-row">
                    <v-btn class="submit-button" @click="tempSave()"
                           style="margin-right:30px;background:#888 !important;" type="button">임시 저장
                    </v-btn>
                    <v-btn class="submit-button" type="submit">게시글 등록</v-btn>
                </div>
            </form>
        </div>

    </div>
</template>


<script>
    import apiService from '@/config/admin/apiService'
    import {
        API_ADMIN_INSERT_BOARD_ARTICLE,
        API_ADMIN_UPDATE_BOARD_ARTICLE,
        API_ADMIN_GET_BOARD_ARTICLE_REGIST,
        API_ADMIN_INSERT_ARTICLE_TEMP_FILE,
        API_ADMIN_INSERT_ARTICLE_TEMP_IMAGE,
        API_ADMIN_TEMP_BOARD_IS_ARTICLE,
        API_ADMIN_TEMP_BOARD_ARTICLE

    } from '@/config/admin/urls'
    import 'quill/dist/quill.core.css'
    import 'quill/dist/quill.snow.css'
    import 'quill/dist/quill.bubble.css'
    import {quillEditor} from 'vue-quill-editor'
    import JQ from 'jquery'
    import UTIL from '@/common/util'

    export default {
        components: {
            quillEditor,
        },
        computed: {
            form() {
                return {
                    bid: this.bid,
                    category: this.category,
                    subject: this.subject,
                }
            },
            editor() {
                return this.$refs.myQuillEditor.quill
            }
        },
        watch: {
            bid(val) {
                for (let brd of this.boardList) {
                    if (val == brd.bid) {
                        this.boardInfo = brd;
                        break;
                    }
                }

                //this.btype = this.boardInfo.btype;
                //this.secretUse = this.boardInfo.secret;
                this.categoryUse = this.boardInfo.categoryUse;
                if (this.categoryUse == 'yes') {
                    for (let item of JSON.parse(this.boardInfo.categoryList)) {
                        if (item.use == 'yes') {
                            this.categoryList.push({text: item.name, value: item.code})
                        }


                    }
                }
                if (this.getInfoFlag) {
                    this.setAddInfo()
                    this.getInfoFlag = false;
                }

            }
        },
        data() {
            return {
                id: '',
                bid: '',
                info: {},
                boardInfo: {},
                boardList: [],
                bidList: [],
                subject: '',
                content: '',
                userInfoShow: false,
                editorOption: {
                    placeholder: '내용을 입력하세요',
                    theme: 'snow',
                    modules: {
                        toolbar: {
                            container: [
                                [{'header': 1}, {'header': 2}],               // custom button values
                                ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
                                ['blockquote', 'code-block'],
                                [{'list': 'ordered'}, {'list': 'bullet'}],
                                [{'script': 'sub'}, {'script': 'super'}],      // superscript/subscript
                                [{'indent': '-1'}, {'indent': '+1'}],          // outdent/indent
                                [{'direction': 'rtl'}],                         // text direction
                                [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
                                [{'header': [1, 2, 3, 4, 5, 6, false]}],
                                [{'color': []}, {'background': []}],          // dropdown with defaults from theme
                                //[{ 'font': [] }],
                                [{'align': []}],
                                ['image'],
                                ['link'],
                                ['clean']
                            ],
                            handlers: {
                                image: this.editorImageHandler,
                            }
                        }
                    }

                },
                btype: '',
                category: '',
                categoryList: [],
                categoryUse: 'no',
                secretUse: 'no',
                secret: '',
                bidDisabled: false,
                imgs: [
                    {
                        isfile: '',
                        file: null,
                        url: ''
                    },

                ],
                imgIndex: 0,
                imgSize: {
                    width: 200,
                    height: 200,
                    text: '200×200'
                },
                keyword: '',
                dFileList: [],
                replyUse: 'yes',
                open: 'yes',
                userNotice: 'no',
                formHasErrors: false,
                serverFlag: false,
                getInfoFlag: false,
                uploadImgType: 'list',
                indexId: '',
                notice: "no",
                userName: '',
                userPass: '',
                imgList: [],
                userId:'',
            };
        },
        created() {
            if (this.$route.params.id != 'new') this.id = this.$route.params.id;
            this.getData();
        },
        methods: {
            getData() {
                const frm = {id: this.id};
                apiService.post(API_ADMIN_GET_BOARD_ARTICLE_REGIST, frm, (res) => {
                    if (res) {
                        this.boardList = res.boardList;
                        for (let item of res.boardList) {
                            if (res.info?.bid && (res.info?.bid == item.bid)) {
                                this.boardInfo = item;
                            }
                            this.bidList.push({text: item.bname, value: item.bid});
                        }
                        if (res.info?.bid) {
                            this.indexId = this.id;
                            this.info = res.info;
                            this.userId = this.info.userId;
                        } else {
                            this.indexId = '';
                            this.userId = '';
                        }

                        if (res.tempArticle) {
                            let params = {
                                type:'confirm',
                                message: '임시저장된 게시글이 존재합니다<br>저장된 글을 불러오겠습니까?',
                                doAction:()=>{
                                    this.info = res.tempArticle;

                                    this.setTempArticle();
                                },
                                cancleAction:() => {
                                    if (this.indexId) {
                                        this.setIsArticle();
                                    }
                                }
                            }
                            this.$eventBus.$emit('modalOpen', params);
                            return;
                        } else {
                            if (this.indexId) {
                                this.setIsArticle();
                            }
                        }

                    }
                })
            },
            setTempArticle() {
                this.getInfoFlag = true;
                this.bid = this.info.bid;
                if(this.indexId) {
                    if (this.userId) {
                        this.userInfoShow = false;
                    } else {
                        this.userInfoShow = true;
                        this.userName = this.info.userName;
                        this.userPass = this.info.userPassword;
                    }
                    this.bidDisabled = true;
                } else {
                    this.userInfoShow = false;
                }
                this.setBaseInfo();
            },
            setIsArticle() {

                this.getInfoFlag = true;
                this.bid = this.info.bid;
                if (this.info.userId) {
                    this.userInfoShow = false;
                } else {
                    this.userInfoShow = true;
                    this.userName = this.info.userName;
                    this.userPass = this.info.userPassword;
                }

                this.bidDisabled = true;
                this.setBaseInfo();
            },
            setBaseInfo() {
                this.subject = this.info.subject;
                this.content = this.info.content;
                this.replyUse = this.info.replyUse;
                this.userNotice = this.info.userNotice;
                this.notice = this.info.notice;
                this.open = this.info.open;


                if (this.info.keywords) {
                    let keywordList = this.info.keywords.split(',');
                    let keywords = [];
                    for (const key of keywordList) {
                        if (key) {
                            keywords.push(key);
                        }
                    }
                    if (keywords.length > 0) {
                        this.keyword = keywords.join(',');
                    }
                }
                if (this.info.dfileOrgNames) {
                    let fileOrgNames = this.info.dfileOrgNames.split(',');
                    let fileNames = this.info.dfileNames.split(',');
                    let searchDfileNames = this.info.searchDfileNames.split(',');
                    let fIndex = 0;
                    for (const fname of fileOrgNames) {
                        let file = fileNames[fIndex]
                        let searchName = searchDfileNames[fIndex]
                        let fData = {
                            orgFileName: fname,
                            fileName: file,
                            searchName: searchName,
                        }
                        this.dFileList.push(fData);
                        fIndex++;
                    }

                }
                if (this.info.orgImgNames) {
                    let orgImgList = this.info.orgImgNames.split(',');
                    let imgs = this.info.imgs.split(',');
                    let searchImgName = this.info.searchImgNames.split(',');
                    let fIndex = 0;
                    for (const imgName of orgImgList) {
                        let file = imgs[fIndex]
                        let searchName = searchImgName[fIndex]
                        this.imgList.push({
                            orgName: imgName,
                            searchName: searchName,
                            name: file
                        });
                        fIndex++;
                    }
                }
            },
            setAddInfo() {
                if (this.boardInfo.categoryUse == 'yes') {
                    if (this.info.category) this.category = this.info.category
                }

            },
            editorImageHandler() {
                this.uploadImgType = 'contentEditor'
                this.$refs.img.click();
            },
            deleteImg(index) {
                this.imgs.splice(index, 1)
                this.imgs.push({isfile: '', file: null, url: ''});

            },
            insertImg(index) {
                this.uploadImgType = 'list'
                this.$refs.img.click();
            },
            addDfile() {
                this.uploadImgType = 'dfile'
                this.$refs.downFile.click();
            },
            dfileUpload() {
                if (this.dFileList.length >= 10) {
                    let params = {
                        message: '첨부파일은 10개 까지만 등록가능합니다',

                    }
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }

                const imgObj = this.$refs.downFile.files;
                let img = imgObj.length > 0 ? imgObj[0] : null;

                const frm = new FormData();
                frm.append('dfile', img);
                apiService.postFile(API_ADMIN_INSERT_ARTICLE_TEMP_FILE, frm, (res) => {
                    const searchName = res.orgFileName.replace(res.ext, '');
                    let fData = {
                        orgFileName: res.orgFileName,
                        searchName: searchName,
                        fileName: res.saveFileUrl,
                    }
                    this.dFileList.push(fData);
                    this.$refs.downFile.value = '';
                })

            },
            imgUpload() {
                const imgObj = this.$refs.img.files;
                let img = imgObj.length > 0 ? imgObj[0] : null;

                const frm = new FormData();
                frm.append('image', img);
                apiService.postFile(API_ADMIN_INSERT_ARTICLE_TEMP_IMAGE, frm, (res) => {

                    switch (this.uploadImgType) {
                        case 'contentEditor':
                            const range = this.editor.getSelection();
                            let url = apiService.imgHost + 'api/controller/board/getArticleImage?imgName=' + encodeURIComponent(res.saveFileUrl)
                            this.editor.insertEmbed(range.index, 'image', url);
                            const searchName = res.orgFileName.replace(res.ext, '');
                            this.imgList.push({
                                orgName: res.orgFileName,
                                searchName: searchName,
                                name: res.saveFileUrl
                            });

                            this.$refs.img.value = '';
                            break;
                        /*
                         case 'list':
                            let imgData = {
                                isfile: '',
                                orgFileName: res.orgFileName,
                                imgName: res.saveFileUrl,
                                url: apiService.imgHost + 'api/controller/board/getArticleImage?imgName=' + encodeURIComponent(res.saveFileUrl)
                            }
                            this.imgs.splice(0, 1, imgData);
                            this.$refs.img.value = '';
                            break;

                         */
                    }

                })

            },
            removeDfile(index) {
                this.dFileList.splice(index, 1)
            },
            onEditorChange({quill, html, text}) {
                // console.log('editor change!', quill, html, text)
                this.content = html
            },
            validate() {

                this.formHasErrors = false
                const updParams = {};
                let firstErrorId = '';
                Object.keys(this.form).forEach(f => {
                    let check = false;
                    switch (f) {
                        case 'category':
                            if (this.categoryUse != 'yes') {
                                check = true;
                            }
                            break;
                    }
                    if (!check) {
                        if (!this.form[f]) {
                            this.formHasErrors = true
                            if (!firstErrorId) firstErrorId = f;
                        }
                        this.$refs[f].validate(true)
                    }
                    updParams[f] = this.form[f];
                })
                if (this.formHasErrors) {
                    UTIL.errorPosition('base', '#' + firstErrorId + '-required', () => {

                    })
                    return;
                }
                if (this.userInfoShow) {
                    if (!this.userName) {
                        this.$refs.userName.validate(true)
                        UTIL.errorPosition('base', '#userinfo-required', () => {

                        })
                        return;
                    }
                    if (!this.userPass) {
                        this.$refs.userPass.validate(true)
                        UTIL.errorPosition('base', '#userinfo-required', () => {

                        })
                        return;
                    }
                    updParams['userName'] = this.userName;
                    updParams['userPassword'] = this.userPass;
                }

                if (!this.content) {
                    let params = {
                        message: '내용을 입력하세요',

                    }
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }

                updParams['content'] = this.content;


                const keywordList = this.keyword.split(',');
                const keywords = [];
                for (const key of keywordList) {
                    if (key) {
                        keywords.push(key.replace(/ /g, ''));
                    }
                }
                if (keywords.length > 10) {
                    let params = {
                        message: '키워드는 10개 까지만 등록가능합니다',

                    }
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }
                let keywordString = '';
                if (keywords.length > 0) {
                    keywordString = keywords.join(',');
                }
                updParams['keywords'] = keywordString;

                const regex = /<img[^>]+src=[\"']?([^>\"']+)[\"']?[^>]*>/g
                let extractUrl = '';
                let inputOrgImgList = [];
                let inputImgList = [];
                let searchImgNames = [];
                while (extractUrl = regex.exec(this.content)) {
                    if (extractUrl[1]) {
                        let imgName = extractUrl[1].replace(apiService.imgHost + 'api/controller/board/getArticleImage?imgName=', '');

                        for (let img of this.imgList) {
                            if (imgName == img.name) {

                                inputOrgImgList.push(img.orgName)
                                inputImgList.push(img.name);
                                searchImgNames.push(img.searchName)
                            }
                        }
                    }
                }

                let imgString = '';
                let orgImgString = '';
                let searchImgNameString = '';
                if (inputOrgImgList.length > 0) {
                    imgString = inputImgList.join(',');
                    orgImgString = inputOrgImgList.join(',');
                    searchImgNameString = searchImgNames.join(',');
                }
                updParams['imgString'] = imgString
                updParams['orgImgString'] = orgImgString
                updParams['searchImgNameString'] = searchImgNameString


                let orgDfileNameString = '';
                let dfileNameString = '';
                let searchDfileNameString = '';
                if (this.dFileList.length > 0) {
                    let orgFileNames = [];
                    let fileNames = [];
                    let searchFileNames = [];
                    for (const item of this.dFileList) {
                        orgFileNames.push(item.orgFileName);
                        fileNames.push(item.fileName);
                        searchFileNames.push(item.searchName)
                    }
                    orgDfileNameString = orgFileNames.join(',');
                    dfileNameString = fileNames.join(',');
                    searchDfileNameString = searchFileNames.join(',');
                }

                updParams['orgDfileNameString'] = orgDfileNameString
                updParams['dfileNameString'] = dfileNameString
                updParams['searchDfileNameString'] = searchDfileNameString


                updParams['replyUse'] = (this.replyUse == 'yes') ? 'yes' : 'no';

                updParams['open'] = (this.open == 'yes') ? 'yes' : 'no';
                updParams['userNotice'] = (this.userNotice == 'yes') ? 'yes' : 'no';
                updParams['notice'] = (this.notice == 'yes') ? 'yes' : 'no';

                if (this.serverFlag) return;
                this.serverFlag = true;
                if (this.indexId && this.info?.bid) {
                    updParams['id'] = this.indexId;
                }
                const url = (this.indexId && this.info?.bid) ? API_ADMIN_UPDATE_BOARD_ARTICLE : API_ADMIN_INSERT_BOARD_ARTICLE;

                this.$eventBus.$emit('overlay', 'open');
                apiService.post(url, updParams, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    if (res.status == 'success') {
                        let msg = '저장'
                        if (this.indexId) {
                            msg = '수정'
                        }
                        let params = {
                            message: '게시글 정보가 ' + msg + ' 되었습니다',
                            doAction: () => {
                                this.$router.push('/admin/run/articleList');
                            }
                        }
                        this.$eventBus.$emit('modalOpen', params);

                    } else {
                        let params = {
                            message: '정보 저장에 실패하였습니다',
                        }
                        this.$eventBus.$emit('modalOpen', params);
                    }
                })
            },
            tempSave() {
                const updParams = {};
                Object.keys(this.form).forEach(f => {
                    updParams[f] = this.form[f];
                })
                if (this.userInfoShow) {
                    if (this.userName) {
                        updParams['userName'] = this.userName;
                    }
                    if (this.userPass) {
                        updParams['userPassword'] = this.userPass;
                    }
                }
                updParams['content'] = this.content;

                const keywordList = this.keyword.split(',');
                const keywords = [];
                for (const key of keywordList) {
                    if (key) {
                        keywords.push(key.replace(/ /g, ''));
                    }
                }
                if (keywords.length > 10) {
                    let params = {
                        message: '키워드는 10개 까지만 등록가능합니다',

                    }
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }
                let keywordString = '';
                if (keywords.length > 0) {
                    keywordString = keywords.join(',');
                }
                updParams['keywords'] = keywordString;

                const regex = /<img[^>]+src=[\"']?([^>\"']+)[\"']?[^>]*>/g
                let extractUrl = '';
                let inputOrgImgList = [];
                let inputImgList = [];
                let searchImgNames = [];
                while (extractUrl = regex.exec(this.content)) {
                    if (extractUrl[1]) {
                        let imgName = extractUrl[1].replace(apiService.imgHost + 'api/controller/board/getArticleImage?imgName=', '');
                        for (let img of this.imgList) {
                            if (imgName == img.name) {
                                inputOrgImgList.push(img.orgName)
                                inputImgList.push(img.name);
                                searchImgNames.push(img.searchName)
                            }
                        }
                    }
                }

                let imgString = '';
                let orgImgString = '';
                let searchImgNameString = '';
                if (inputOrgImgList.length > 0) {
                    imgString = inputImgList.join(',');
                    orgImgString = inputOrgImgList.join(',');
                    searchImgNameString = searchImgNames.join(',');
                }
                updParams['imgString'] = imgString
                updParams['orgImgString'] = orgImgString
                updParams['searchImgNameString'] = searchImgNameString


                let orgDfileNameString = '';
                let dfileNameString = '';
                let searchDfileNameString = '';
                if (this.dFileList.length > 0) {
                    let orgFileNames = [];
                    let fileNames = [];
                    let searchFileNames = [];
                    for (const item of this.dFileList) {
                        orgFileNames.push(item.orgFileName);
                        fileNames.push(item.fileName);
                        searchFileNames.push(item.searchName)
                    }
                    orgDfileNameString = orgFileNames.join(',');
                    dfileNameString = fileNames.join(',');
                    searchDfileNameString = searchFileNames.join(',');
                }

                updParams['orgDfileNameString'] = orgDfileNameString
                updParams['dfileNameString'] = dfileNameString
                updParams['searchDfileNameString'] = searchDfileNameString


                updParams['replyUse'] = (this.replyUse == 'yes') ? 'yes' : 'no';

                updParams['open'] = (this.open == 'yes') ? 'yes' : 'no';
                updParams['userNotice'] = (this.userNotice == 'yes') ? 'yes' : 'no';
                updParams['notice'] = (this.notice == 'yes') ? 'yes' : 'no';

                if (this.serverFlag) return;
                this.serverFlag = true;
                if (this.indexId) {
                    updParams['id'] = this.indexId;
                }
                const url = (this.indexId) ? API_ADMIN_TEMP_BOARD_IS_ARTICLE : API_ADMIN_TEMP_BOARD_ARTICLE;

                this.$eventBus.$emit('overlay', 'open');
                apiService.post(url, updParams, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    if (res.status == 'success') {
                        let params = {
                            message: '게시글 정보가 임시저장 되었습니다',
                        }
                        this.$eventBus.$emit('modalOpen', params);

                    } else {
                        let params = {
                            message: '임시저장에 실패하였습니다',
                        }
                        this.$eventBus.$emit('modalOpen', params);
                    }
                })
            }
        },
        mounted() {
            JQ('.quill-content-box .ql-editor').css('min-height', '400px');

        }

    }
</script>

